@import 'ihover.min.css';
html,
body,
input {
  font-family: "Microsoft YaHei", "SimSun", "微软雅黑", "宋体", Candara, "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}
html,
body {
  font-size: 100px;
  margin: 0;
  height: 100%;
}
*,
body,
a,
address,
b,
big,
blockquote,
body,
center,
cite,
code,
dd,
del,
div,
dl,
dt,
em,
fieldset,
font,
form,
h1,
h2,
h3,
h4,
h5,
h6,
html,
i,
iframe,
img,
ins,
label,
legend,
li,
ol,
p,
pre,
small,
span,
strong,
u,
ul,
var {
  margin: 0;
  padding: 0;
}
div {
  display: block;
  font-size: 0.14rem;
}
label span {
  font-size: 0.13rem;
  font-weight: normal;
}
label {
  font-weight: 500;
}
.outerHeight {
  margin: 0 auto;
  height: auto;
  min-height: calc(100vh - 2rem);
}
.welcome {
  text-align: center;
}
.welcome h3 {
  color: #666;
  font-weight: 300;
  margin-top: 40px;
  padding: 40px 0px;
}
.welcome pre {
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 10px;
  width: 400px;
  height: 400px;
  text-align: left;
  border: 1px solid #1ea57a;
  border-radius: 2px;
}
.ant-carousel .slick-slide {
  text-align: center;
  overflow: hidden;
}
.ant-carousel .slick-slide h3 {
  color: #fff;
}
.ih-item.square {
  width: auto;
  height: auto;
}



